<template>
  <div class="content">
    <div class="contentTop" v-if="moneyList !=null">
      <p>已为<span>{{moneyList.CUST_SUM | farmatDataWan}}</span>亿用户撮合融资<span>{{moneyList.INVEST_SUM | farmatDataYi}}</span>亿元，赚取<span>{{moneyList.INCOME_SUM | farmatDataWan}}</span>万元收益</p>
    </div>
    <div class="contentMdl">
      <ul class="contentMdl clearfix">
        <li class="list fl br10" v-for="item in itemList">
          <ul class="listUl ">
            <li>
              <img :src="item.listUrl" alt="">
              <p class="listTitle">{{item.listTitle}}</p>
              <p class="listSummery">{{item.listSummery}}</p>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!--推荐产品-->
    <div class="groom">
      <p class="groomTle">
        <img src="../../assets/images/index/groom.png" alt="">
      </p>
      <!--天天盈产品-->
      <div class="current">
        <ul class="investFl fl">
          <li><h2>8<span>%</span></h2></li>
          <li><p>年化收益率</p></li>
        </ul>
        <div class="currentLine fl"></div>
        <ul class="currentfr fl">
          <li>
            <h3>天天盈 <img src="../../assets/images/index/current.png" alt=""></h3>
          </li>
          <li>起投金额<span>100.00</span>元</li>
        </ul>
      </div>
      <!--月月盈和定投盈-->
      <div class="regular clearfix">
        <ul class="investFl fl">
          <li>
            <h1>月月盈</h1>
          </li>
          <li>
            <img src="../../assets/images/index/regular.png" alt="">
          </li>
          <li><h2>10<span>%</span>~14<span>%</span></h2></li>
          <li><p>年化收益率</p></li>
        </ul>
        <div class="regularLine fl"></div>
        <ul class="investfr fl">
          <li>
            <h1>定存盈</h1>
          </li>
          <li>
            <img src="../../assets/images/index/mouthInvest.png" alt="">
          </li>
          <li><h2>10<span>%</span>~15<span>%</span></h2></li>
          <li><p>年化收益率</p></li>
        </ul>
      </div>
    </div>
    <!--新闻咨询-->
    <div class="high"></div>
    <div class="news" id="news_div">
      <p class="groomTle">
        <img src="../../assets/images/index/newsImg.png" alt="">
      </p>
      <div class="newsOne borLine clearfix" v-for="(list, index) in newsList" @click="goNews1(index)">
        <div class="newsFl fl">
          <img :src="list.listUrl" alt="">
        </div>
        <div class="newsFr fr">
          <h1 v-text="list.listTitle"></h1>
          <p v-text="list.listSummery"></p>
        </div>
      </div>
    </div>
    <!--马上注册领红包-->
    <div class="register">
      <div class="clearfix">
        <ul class="registerFl registerFlOne fl">
          <li class="registerLi">
            <img src="../../assets/images/index/register.png" alt="">
          </li>
          <li>
            <h3>注册及认证</h3>
          </li>
          <li>
            <p>账户注册、手机和实名认证</p>
          </li>
        </ul>
        <div class="icongo fl">
          <img src="../../assets/images/index/icongo.png" alt="">
        </div>
        <ul class="registerFl registerFlTwo fl">
          <li class="registerLi">
            <img src="../../assets/images/index/recharge.png" alt="">
          </li>
          <li>
            <h3 class="color268">账户充值</h3>
          </li>
          <li>
            <p>通过网银进行账户充值</p>
          </li>
        </ul>
        <div class="icongo fl">
          <img src="../../assets/images/index/icongo.png" alt="">
        </div>
        <ul class="registerFl registerFlThree fl">
          <li class="registerLi">
            <img src="../../assets/images/index/income.png" alt="">
          </li>
          <li>
            <h3 class="color307">投资收益</h3>
          </li>
          <li>
            <p>选择项目投资，坐享收益</p>
          </li>
        </ul>
      </div>
      <div class="btnCenter">
        <router-link to="/register" class="br10">马上注册领红包</router-link>
      </div>
    </div>
  </div>


</template>

<script>
  import { jwtServer } from '../../assets/js/jwtServers'
  import '../../assets/js/filter'

  export default {
    data () {
      return {
        moneyList:null,
        itemList:[
          {
            listUrl:require("../../assets/images/index/jinwutong.png"),
            listTitle:'关于我们',
            listSummery:'安全运营30年'
          },
          {
            listUrl:require("../../assets/images/index/xinwang.png"),
            listTitle:'银行存管',
            listSummery:'践行合规'
          },
          {
            listUrl:require("../../assets/images/index/safe.png"),
            listTitle:'安全保障',
            listSummery:'三级等保'
          }
        ],
        newsList:[
          {
            listUrl:require("../../assets/images/index/news1.png"),
            listTitle:'中国中小企业协会普惠金融促进工作委员会在京成立',
            listSummery:'2017-07-24 16:07:39'
          },
          {
            listUrl:require("../../assets/images/index/news2.png"),
            listTitle:'普惠通携手上海官途   开启“互联网金融+旅游”大时代',
            listSummery:'2016-03-15 15:09:35'
          }
        ]
      }
    },
    mounted () {
      this.getMoney()
    },
    methods: {
      getMoney () {
        console.log(this.domain)
        jwtServer.globalPostData('/apis/msgcenter/query/1.0/queryMainPageTotalData/1.0',jwtServer.submitData({})).then((data)=> {
          this.moneyList =data.result.main_data.data[0];
          console.log(this.moneyList);
        })
      },
      goNews1 (index) {
        if(index == 0){
          window.open(this.domain +"notice/getNotice.do?noticeId=459")
        }else if(index == 1) {
          window.open(this.domain+"notice/getNotice.do?noticeId=325")
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  @import "../../assets/css/index";
</style>



