<template>
  <div class="home_footer">
    <div class="link_button_container">
      <li v-for="item in linkButtonData">
        <a :href="item.linkUrl" :target="item.d_target">
          <div class="link_button" style="display: table-cell">
            <img :src="item.url "/>
            <p style="margin-top: 0.10rem">{{item.title}}</p>
          </div>
        </a>
      </li>
    </div>
    <p class="divide_line_vertical"></p>
    <div class="net_info">
      <li v-for="item in companyInfoData">
       <p> {{ item.title}}</p>
      </li>
    </div>

    <div id="ad" class="ad_container">
      <div class="close_btn"  @click="showAdView(false)">
        <img src="../../assets/images/index/close.png" style="vertical-align: middle"/>
      </div>

      <a class="download">

        <span style="height: 1.54rem;line-height: 1.54rem; float: left;margin-right: 0.3rem;font-size: 0.26rem">
            <img src="../../assets/images/float_logo.png" style="width: 0.8rem;height: 0.8rem;vertical-align: middle"/>
        </span>
          <div style="vertical-align: middle;padding-top:0.2rem;padding-bottom: 0.2rem;text-align: center"
              >
           <p style="position: relative;color: white;font-size: 0.26rem;line-height: 0.6rem;">掌握理财，随时随地，快人一步</p>
            <span class="button">下载金梧桐APP</span>
          </div>

      </a>
      </div>

  </div>
</template>
<script>
  export default {
    data () {
      return {
        linkButtonData:[
          {
            url:require("../../assets/images/index/icon_pc.png"),
            title:"电脑版",
            linkUrl:this.domain,
            d_target:"_blank"
          },{
            url:require("../../assets/images/index/icon_news.png"),
            title:"新闻资讯",
            linkUrl:"#news_div",
            d_target:"_self"
          },{
            url:require("../../assets/images/index/icon_about_us.png"),
            title:"关于我们",
            linkUrl:this.domain+"about/about.do?index=0",
            d_target:"_blank"
          },{
            url:require("../../assets/images/index/icon_connect_us.png"),
            title:"联系我们",
            linkUrl:this.domain+"about/about.do?index=0#section-6",
            d_target:"_blank"
          },{
            url:require("../../assets/images/index/icon_dowonload_app.png"),
            title:"下载APP",
            linkUrl:this.domain+"hander/download1.do",
            d_target:"_blank"
          }
        ],
        companyInfoData:[
          {
            title:"©2017金梧桐 All Rights Reserved"
          },{
            title:"北京普惠通数据信息技术有限公司  |  京ICP备14046794号 -1"
          },{
            title:"服务热线 : 400-159-8866 (9:00-21:00)"
          },{
            title:"投资有风险 出借需谨慎"
          }
        ]


      }
    },
    mounted () {

    },
    methods:{
      showAdView:function(isShow){
        let doc =  document.getElementById('ad');
        let footer = document.getElementsByClassName("home_footer")[0];

          if(isShow){
            doc.style.visibility= 'visible';
            if(footer){
              footer.style.paddingBottom = "1.74rem";
            }
          }else{
            doc.style.visibility= "hidden";
            if(footer){
              footer.style.paddingBottom = "0.54rem";
            }
          }
      }
    }

  }

</script>

<style lang="less" scoped>
  @import "../../assets/css/common.css";
  @import "../../assets/css/commom.less";
  @font_color:#666666;
  @font_size:0.22rem;

  .button{
    position: relative; /** 相对布局 **/
    background-color: #fb4747;
    color: white;
    font-size: 0.28rem;
    display: inherit;
    overflow: hidden;
    margin-right: 0.34rem;
    padding-top: 0.1rem;
    padding-bottom: 0.1rem;

  }
  .button:active{
    top: 2px; /**向下偏移2px **/
    background-color: #d22323;

  }
  .ad_container{
    width: 100%;
    max-width: @device_max_width;
    background-color:#000000;/* IE6和部分IE7内核的浏览器(如QQ浏览器)下颜色被覆盖 */
    background-color:rgba(0,0,0,0.6); /* IE6和部分IE7内核的浏览器(如QQ浏览器)会读懂，但解析为透明 */
    height: 1.54rem;
    position: fixed;//屏幕底端固定
    bottom: 0px;
    z-index:1;//不被内容盖住
  }
  .ad_container .close_btn{
    width: 0.8rem;
    height: 1.54rem;
    float: left;
    line-height: 1.54rem;
    text-align: center;
    outline:0;

  }
  .ad_container .download{
    overflow: hidden;
    background-color: red;
  }
  .ad_container img{
    width:0.4rem;
    height: 0.4rem;
  }

  .net_info{
    margin-top: 0.15rem;
  }

  .net_info li{
    list-style:none
  }
  .net_info p{
    width:100%;
    text-align: center;
    height: 0.4rem;
    line-height: 0.4rem;
  }
  .divide_line_vertical{
    width: auto;
    background-color: #4d4e4f;
    height: 1px;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  a{
    text-decoration:none;
    color: @font_color;
  }
  .link_button img{
    max-height: 0.44rem;
    max-width: 0.44rem;
    opacity:0.4;
    filter:alpha(opacity=40)/*  透明度 针对 IE8 以及更早的版本 */
  }

  .link_button_container{
    width: 100%;
    display: inline-flex;
    height: 1.4rem;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding-left: 0.25rem;
    padding-right: 0.25rem;
    padding-top: 0.05rem;
  }
  .link_button{
    display: table;
    width: 1.4rem;
    height: 1.4rem;
    text-align: center;
    vertical-align: middle;
  }
  .home_footer{
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    padding-bottom: 1.74rem;
    background-color: #2f3132;
    font-size: @font_size;
    color: @font_color;
    display: block;
  }
  .link_button_container li{
    display:inline;
    float:left;
    list-style:none
  }

</style>



